<template>
    <v-dialog
       v-model="dialog"
       width="500"
       height="1000"
     >
       <template v-slot:activator="{ on, attrs }">
         <v-btn
           color="white"
           dark
           icon
           v-bind="attrs"
           v-on="on"
         >
           <v-icon aria-hidden="false">
             mdi-plus
           </v-icon>
         </v-btn>
       </template>
       <v-card>
         <v-card-text>
           <div class="row">
            <v-col md="6">
              <router-link :to="{ name: 'CreateNewInstance'}">
                <v-hover
                  v-slot="{ hover }"
                  open-delay="200"

                >
                  <v-card
                     :elevation="hover ? 16 : 2"
                     :class="{ 'on-hover': hover }"
                     class="mx-auto"
                     height="150"
                     max-width="150"
                  >
                    <v-card-text class="font-weight-medium mt-12 text-center text-subtitle-1">
                       <v-img
                         lazy-src="@/assets/db.png"
                         max-height="150"
                         max-width="250"
                         src="@/assets/db.png"
                       >
                        <v-expand-transition>
                          <div
                             v-if="hover"
                             class="d-flex transition-fast-in-fast-out white darken-2 v-card--reveal text-h6 blue--text"
                             style="height: 100%; justify-content: center; align-items: center;"
                             @click="alert_show()"
                          >
                             <h5><b>New instance</b></h5>
                          </div>
                        </v-expand-transition>
                       </v-img>
                    </v-card-text>
                  </v-card>
                </v-hover>
              </router-link>
            </v-col>
            <v-col md="6">
                <v-hover
                  v-slot="{ hover }"
                  open-delay="200"
                >
                  <v-card
                     :elevation="hover ? 16 : 2"
                     :class="{ 'on-hover': hover }"
                     class="mx-auto"
                     height="150"
                     max-width="150"
                  >
                    <v-card-text class="font-weight-medium mt-12 text-center text-subtitle-1">
                       <v-img
                         lazy-src="@/assets/sample.png"
                         max-height="150"
                         max-width="250"
                         src="@/assets/sample.png"
                       >
                        <v-expand-transition>
                          <div
                             v-if="hover"
                             class="d-flex transition-fast-in-fast-out white darken-2 v-card--reveal text-h6 blue--text"
                             style="height: 100%; justify-content: center; align-items: center;"
                             @click="create_sample()"
                          >
                             <h5><b>Take sample</b></h5>
                          </div>
                        </v-expand-transition>
                       </v-img>
                    </v-card-text>
                  </v-card>
                </v-hover>
            </v-col>
           </div>
         </v-card-text>
         <v-card-actions>
           <v-spacer></v-spacer>

         </v-card-actions>
       </v-card>
       <v-snackbar
         v-model="snackbar"
       >
             JobId for sample: {{ jobUuid }}
         <template v-slot:action="{ attrs }">
            <v-btn
                 color="pink"
                 text
                 v-bind="attrs"
                 @click="snackbar = false"
            >
              Close
            </v-btn>
         </template>
       </v-snackbar>
     </v-dialog>
</template>

<script>
  import {mapGetters} from 'vuex'
  import apiClient from "../../plugins/ApiClient.js"

  export default {
    name: 'CreateEntityDialog',
    components: {},
    data () {
      return {
        dialog: false,
        jobUuid: '',
        snackbar: false,
        items: [
            "Project",
            "Test"
        ],
        data: {
            name: '',
            type: ''
        }
      }
    },
    methods: {
       ...mapGetters(['GET_SELECT_SERVER']),
       create_sample(){
          let data = this.GET_SELECT_SERVER();
          let serversIds = [data[0].serverId]
          apiClient.post_create_sample(serversIds, localStorage.getItem('pg_profile_token')).then((response)=>{
              console.log(response.data)
              this.jobUuid = response.data[0],
              this.snackbar = true;
          })
       }
    }
  }
</script>