<template>
  <v-row>
    <v-col cols="9">
    </v-col>
    <v-col cols="12">
      <v-card>
        <v-card-title>
          Samples
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="GET_SAMPLES()"
          :search="search"
          item-key="uuid"
        >
            <template v-slot:item.first_sample_start="{ item }">
               <v-checkbox
                v-bind:value="item"
                v-model="start_first_sample"
               ></v-checkbox>
            </template>
            <template v-slot:item.first_sample_end = "{ item }">
               <v-checkbox
                v-model="end_first_sample"
                v-bind:value="item"
               ></v-checkbox>
            </template>
            <template v-slot:item.second_sample_start = "{ item }">
               <v-checkbox
                v-model="start_second_sample"
                v-bind:value="item"
               ></v-checkbox>
            </template>
            <template v-slot:item.second_sample_end = "{ item }">
               <v-checkbox
                v-model="end_second_sample"
                v-bind:value="item"
               ></v-checkbox>
            </template>
        </v-data-table>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
  import {mapGetters} from 'vuex'
  import {mapMutations} from 'vuex'
  import {mapActions} from 'vuex'

  export default {
    name: 'SamplesDataTable',
    data () {
      return {
        search: '',
        start_first_sample: [],
        end_first_sample: [],
        start_second_sample: [],
        end_second_sample: [],
        headers: [
          { text: 'Sample id', value: 'sample_id' },
          { text: 'Server id', value: 'server_id' },
          { text: 'Server name', value: 'server_name' },
          { text: 'Server create date', value: 'create_date' },
          { text: 'Sample time', value: 'sample_time' },
          { text: 'first_sample_start', value: 'first_sample_start'},
          { text: 'first_sample_end', value: 'first_sample_end'},
          { text: 'second_sample_start', value: 'second_sample_start'},
          { text: 'second_sample_end', value: 'second_sample_end'},
        ]
      }
    },
    created(){
        this.REFRESH_SAMPLES()
    },

    methods: {
       ...mapGetters(['GET_SAMPLES']),
       ...mapMutations([
        'SET_START_FIRST_SAMPLE_TO_STATE',
        'SET_END_FIRST_SAMPLE_TO_STATE',
        'SET_START_SECOND_SAMPLE_TO_STATE',
        'SET_END_SECOND_SAMPLE_TO_STATE'
       ]),
       ...mapActions(['REFRESH_SAMPLES'])
    },
    watch: {
        start_first_sample() {
          this.SET_START_FIRST_SAMPLE_TO_STATE(this.start_first_sample)
        },
        end_first_sample() {
          this.SET_END_FIRST_SAMPLE_TO_STATE(this.end_first_sample)
        },
        start_second_sample() {
          this.SET_START_SECOND_SAMPLE_TO_STATE(this.start_second_sample)
        },
        end_second_sample() {
          this.SET_END_SECOND_SAMPLE_TO_STATE(this.end_second_sample)
        }

      },
  }
</script>