<template>
    <v-dialog
       v-model="dialog"
       persistent
       max-width="290"
     >
       <template v-slot:activator="{ on, attrs }">
         <v-btn
           color="white"
           dark
           icon
           v-bind="attrs"
           v-on="on"
         >
           <v-icon aria-hidden="false">
             mdi-logout
           </v-icon>
         </v-btn>
       </template>
       <v-card>
         <v-card-title class="text-h5">
           Logout
         </v-card-title>
         <v-card-text>
            Are you sure you want to exit now?
         </v-card-text>
         <v-card-actions>
           <v-spacer></v-spacer>
           <v-btn
             color="green darken-1"
             text
             @click="dialog = false"
           >
             Not now
           </v-btn>
           <v-btn
             color="green darken-1"
             text
             @click="logout()"
           >
             Logout
           </v-btn>
         </v-card-actions>
       </v-card>
     </v-dialog>
</template>

<script>
  export default {
    name: 'LogoutDialog',
    data () {
      return {
        dialog: false,
      }
    },
    methods: {
        logout(){
            localStorage.removeItem('pg_profile_token');
            this.dialog = false;
            this.$router.push({ path: '/login' })
        }
    }
  }
</script>