<template>
    <v-app primary>
      <v-app-bar
        app
        color="light-blue darken-4"
        dark
      >
        <v-app-bar-nav-icon @click="drawer=true"></v-app-bar-nav-icon>
        <v-avatar :tile="true" size="36">
          <v-img
            src="@/assets/pg_logo.png" alt="logo"
          ></v-img>
        </v-avatar>
        <v-spacer>
        </v-spacer>
        <LogoutDialog/>
      </v-app-bar>
      <v-navigation-drawer
        absolute
        temporary
        v-model="drawer"
      >
        <NavigationDrawer/>
      </v-navigation-drawer>
      <v-main>
        <v-container fluid>
            <v-row>

              <v-col cols="12">
                <v-stepper v-model="e1" cols="6">
                    <v-stepper-header>
                      <v-stepper-step
                        :complete="e1 > 1"
                        step="1"
                      >
                        Select first server and sample interval
                      </v-stepper-step>
                      <v-divider></v-divider>
                      <v-stepper-step
                        :complete="e1 > 2"
                        step="2"
                      >
                        Select second server and sample interval
                      </v-stepper-step>
                      <v-divider></v-divider>
                      <v-stepper-step step="3">
                        Result query compare
                      </v-stepper-step>
                    </v-stepper-header>
                    <v-stepper-items>
                      <v-stepper-content step="1">
                        <v-card
                          class="mb-12" outlined
                        >
                           <SelectServerComponent v-bind:step_num="e1"/>
                        </v-card>
                        <v-btn
                          color="primary"
                          @click="get_first_queries()"
                        >
                          Continue
                        </v-btn>
                        <v-btn text>
                          Cancel
                        </v-btn>
                      </v-stepper-content>
                      <v-stepper-content step="2">
                        <v-card
                          class="mb-12" outlined
                        >
                            <SelectServerComponent v-bind:step_num="e1"/>
                        </v-card>
                        <v-btn
                          color="primary"
                          @click="get_second_queries()"
                        >
                          Continue
                        </v-btn>
                        <v-btn text>
                          Cancel
                        </v-btn>
                      </v-stepper-content>
                      <v-stepper-content step="3">
                        <v-card class="mb-12 mt-4" outlined>
                            <v-row class="mt-4 mb-4 ml-2 mr-2">
                               <v-col>
                                <v-card outlined>
                                  <v-card-title>
                                    <small>First sample entity</small>
                                  </v-card-title>
                                  <v-card-text v-if="this.$store.state.first_queries_compare">
                                    <p><b>Server name: </b> {{this.$store.state.first_queries_compare.server_name}} [ {{this.$store.state.first_queries_compare.server_id}} ]</p>
                                    <p><b>Sample entity: </b> {{this.$store.state.first_queries_compare.sample_time}} [ {{this.$store.state.first_queries_compare.sample_id}} ]</p>
                                  </v-card-text>
                                </v-card>
                               </v-col>
                               <v-col>
                                <v-card outlined>
                                  <v-card-title>
                                    <small>Second sample entity</small>
                                  </v-card-title>
                                  <v-card-text v-if="this.$store.state.second_queries_compare">
                                     <p><b>Server name: </b> {{this.$store.state.second_queries_compare.server_name}} [ {{this.$store.state.second_queries_compare.server_id}} ]</p>
                                     <p><b>Sample entity: </b> {{this.$store.state.second_queries_compare.sample_time}} [ {{this.$store.state.second_queries_compare.sample_id}} ]</p>
                                  </v-card-text>
                                </v-card>
                               </v-col>
                            </v-row>
                            <v-row>
                               <QueryCompareComponent/>
                            </v-row>
                        </v-card>
                        <v-btn
                          color="primary"
                          @click="e1 = 1"
                        >
                          Continue
                        </v-btn>
                        <v-btn text>
                          Cancel
                        </v-btn>
                      </v-stepper-content>
                    </v-stepper-items>
                  </v-stepper>
              </v-col>
            </v-row>
        </v-container>
      </v-main>
    </v-app>
</template>

<script>
  import NavigationDrawer from '@/components/NavigationDrawer.vue'
  import LogoutDialog from '@/components/dialogs/LogoutDialog.vue'
  import SelectServerComponent from '@/components/SelectServerComponent.vue'
  import QueryCompareComponent from '@/components/QueryCompareComponent.vue'
  import {mapGetters} from 'vuex'
  import {mapMutations} from 'vuex'
  import {mapActions} from 'vuex'

  export default {
    name: 'QueryAnalyse',
    components: {
        NavigationDrawer,
        LogoutDialog,
        SelectServerComponent,
        QueryCompareComponent
    },
    created(){
       if (!localStorage.getItem('pg_profile_token')){
          this.$router.push({ path: '/login' })
       }
       this.first_entity_data['sample']=this.SET_FIRST_Q_TO_STATE();
       this.second_entity_data['sample']=this.GET_SECOND_Q();

       let servers = this.GET_SERVERS();

       for (let index in servers.length){
           let entity = servers[index]
           console.log(entity)
       }
    },
    mounted(){
       if (!localStorage.getItem('pg_profile_token')){
          this.$router.push({ path: '/login' })
       }
       this.first_entity_data['sample']=this.SET_FIRST_Q_TO_STATE();
       this.second_entity_data['sample']=this.GET_SECOND_Q();

       let servers = this.GET_SERVERS();

       for (let index in servers.length){
           let entity = servers[index]
           console.log(entity)
       }
    },
    data: () => ({
      e1: 1,
      drawer: false,
      items: [
        {
          text: 'Queries',
          disabled: true,
          href: '/',
        }
      ],
      first_entity_data: {},
      second_entity_data: {},
        series_hit_queryid: [5, 1214],
        chartOptions: {
            chart: {
               width: 250,
               type: 'pie',
            },
            labels: ['diff_queryid', 'total'],
            responsive: [
                {
                    breakpoint: 480,
                    options: {
                        chart: {
                            width: 200
                        },
                        legend: {
                          position: 'top'
                        }
                    }
                }
            ]
        },
    }),
    methods: {
      ...mapGetters([
          'GET_SAMPLES',
          'GET_SERVERS',
          'GET_FIRST_Q',
          'GET_SECOND_Q'
      ]),
      ...mapMutations([
          'SET_FIRST_Q_TO_STATE',
          'SET_SECOND_Q_TO_STATE',
          'SET_SERVER_INFO_STATE',
          'SET_COMPARE_QUERY_TO_STATE'
      ]),
      ...mapActions(['REFRESH_SAMPLES','GET_QUERIES_FOR_FIRST','GET_QUERIES_FOR_SECOND', 'GET_MISS_QUERIES']),
      get_first_queries(){
        this.GET_QUERIES_FOR_FIRST()
        this.e1 = 2
      },
      get_second_queries(){
        this.GET_QUERIES_FOR_SECOND()
        this.e1 = 3
        this.SET_COMPARE_QUERY_TO_STATE()
        this.GET_MISS_QUERIES()
      }
    }
  }
</script>
