<template>
   <v-row>
    <v-col class="col-12 mt-2 mr-2">
      <v-card>
        <v-card-title>
          Reports
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="GET_REPORTS()"
          item-key="uuid"
          :search="search"
        >
            <template v-slot:item.result="{item}">
                 <v-btn
                  color="primary"
                  outlined
                  small
                  :href="build_results_url(item.result)"
                >
                  Show report
                </v-btn>
            </template>
            <template v-slot:item.status="{item}">
                <v-chip
                   class="ma-2"
                   :color="colorState(item.status)"
                   outlined
                   label
                >
                   {{item.status}}
                </v-chip>
            </template>
        </v-data-table>
      </v-card>
    </v-col>
   </v-row>
</template>

<script>
  import {mapGetters} from 'vuex'
  import {mapActions} from 'vuex'

  export default {
    name: 'JobsContentView',
    data () {
      return {
        search: '',
        headers: [
                  { text: 'uuid', value: 'uuid' },
                  { text: 'name', value: 'name' },
                  { text: 'Create date', value: 'startDate' },
                  { text: 'End date', value: 'endDate' },
                  { text: 'status', value: 'status' },
                  { text: 'result' , value: 'result'}
        ],
      }
    },
    methods: {
        ...mapGetters(['GET_REPORTS']),
        ...mapActions(['REFRESH_JOBS']),
        build_results_url(result){
            return this.$store.state.home_url + '/profiler/' + result
        },
        colorState(status){
            if (status=='SUCCESS'){
                return 'success'
            } else {
                return 'error'
            }
        }
    },
    created(){
        this.REFRESH_JOBS()
    }
  }
</script>