<template>
    <v-app primary>
      <v-app-bar
        app
        color="light-blue darken-4"
        dark
      >
        <v-app-bar-nav-icon @click="drawer=true"></v-app-bar-nav-icon>
        <v-avatar :tile="true" size="36">
          <v-img
            src="@/assets/pg_logo.png" alt="logo"
          ></v-img>
        </v-avatar>
        <v-spacer>
        </v-spacer>
        <CreateEntityDialog/>
        <LogoutDialog/>
      </v-app-bar>
      <v-navigation-drawer
        absolute
        temporary
        v-model="drawer"
      >
        <NavigationDrawer/>
      </v-navigation-drawer>
      <v-main>
        <v-breadcrumbs :items="items" large></v-breadcrumbs>
        <v-container fluid>
          <v-row >
              <v-col cols="8">
                 <div id="chart">
                       <apexchart
                        type="rangeBar"
                        height="450"
                        :options="chartOptions"
                        :series="get_series(
                            this.$store.state.start_first_sample,
                            this.$store.state.end_first_sample,
                            this.$store.state.start_second_sample,
                            this.$store.state.end_second_sample,
                            this.$store.state.sSelect
                        )"
                       ></apexchart>
                 </div>
              </v-col>
              <v-col>
                <v-card outlined>
                   <v-card-text>
                    <div class="text--primary">
                        <v-text-field
                           label="report name"
                           hide-details="auto"
                           v-model="job_name"
                        ></v-text-field>
                    </div>
                   </v-card-text>
                   <v-card-actions class="text-right">
                        <v-btn
                          color="primary"
                          outlined
                          small
                          @click="create_report()"
                        >
                           Create report
                        </v-btn>
                        <v-spacer></v-spacer>
                   </v-card-actions>
                </v-card>
                <v-card outlined class="mt-4">
                  <v-card-title>
                    Short job description
                  </v-card-title>
                  <v-card-text>
                    <p><b>Server:</b> {{get_server_info(this.$store.state.sSelect[0])}}</p>
                    <p><b>First interval:</b> {{get_first_report_interval(this.$store.state.end_first_sample[0],this.$store.state.start_first_sample[0]) }}</p>
                    <p><b>Second interval:</b> {{get_first_report_interval(this.$store.state.end_second_sample[0],this.$store.state.start_second_sample[0]) }}</p>
                  </v-card-text>
                </v-card>
              </v-col>
          </v-row>
          <v-tabs v-model="tab">
            <v-tab href="#tab-1">Servers</v-tab>
            <v-tab href="#tab-2">Samples</v-tab>
            <v-tab href="#tab-3">Results</v-tab>
          </v-tabs>
          <v-tabs-items v-model="tab">
            <v-tab-item value="tab-1">
                <ProjectsDataTable/>
            </v-tab-item>
            <v-tab-item value="tab-2">
                <SamplesDataTable/>
            </v-tab-item>
            <v-tab-item value="tab-3">
                <JobsContentView/>
            </v-tab-item>
          </v-tabs-items>
        </v-container>
      </v-main>
      <v-snackbar
        v-model="snackbar"
      >
            JobId for pg_profile report: {{ job_id }}
        <template v-slot:action="{ attrs }">
          <v-btn
             color="pink"
             text
             v-bind="attrs"
             @click="snackbar = false"
          >
             Close
          </v-btn>
        </template>
      </v-snackbar>
    </v-app>
</template>

<script>
  import NavigationDrawer from '@/components/NavigationDrawer.vue'
  import CreateEntityDialog from '@/components/dialogs/CreateEntityDialog.vue'
  import LogoutDialog from '@/components/dialogs/LogoutDialog.vue'
  import ProjectsDataTable from '@/components/ProjectsDataTable.vue'
  import SamplesDataTable from '@/components/SamplesDataTable.vue'
  import JobsContentView from '@/components/JobsContentView.vue'
  import VueApexCharts from 'vue-apexcharts'
  import {mapGetters} from 'vuex'
  import apiClient from "../../plugins/ApiClient.js"

  export default {
    name: 'DashboardPage',
    components: {
        NavigationDrawer,
        CreateEntityDialog,
        LogoutDialog,
        ProjectsDataTable,
        SamplesDataTable,
        apexchart: VueApexCharts,
        JobsContentView
    },
    computed: {

    },
    data: () => ({
      drawer: false,
      group: null,
      single: false,
      compare: false,
      first_sample: '',
      second_sample: '',
      third_sample: '',
      fourth_sample: '',
      job_name: '',
      job_id: '',
      snackbar: false,
      items: [
              {
                text: 'Servers',
                disabled: true,
                href: '/',
              }
      ],
      tab: null,
      series: [],
      chartOptions: {
            chart: {
               height: 450,
               type: 'rangeBar'
            },
            plotOptions: {
               bar: {
                   horizontal: true,
                   barHeight: '80%'
               }
            },
            xaxis: {
               type: 'datetime'
            },
            stroke: {
               width: 1
            },
            fill: {
               type: 'solid',
               opacity: 0.6
            },
            legend: {
               position: 'right',
               horizontalAlign: 'left'
            }
        },
    }),
    created(){
        if (!localStorage.getItem('pg_profile_token')){
            this.$router.push({ path: '/login' })
        }
    },
    methods: {
        ...mapGetters([
            'GET_SELECT_SERVER',
            'GET_START_FIRST_SAMPLE',
            'GET_END_FIRST_SAMPLE',
            'GET_START_SECOND_SAMPLE',
            'GET_END_SECOND_SAMPLE'
        ]),
        create_report(){
            let servers = [this.$store.state.sSelect[0].serverId];
            let samples = [];
            if (this.$store.state.start_first_sample.length > 0 && this.$store.state.end_first_sample.length > 0){
                let first_interval = []
                first_interval.push(this.$store.state.start_first_sample[0].sample_id);
                first_interval.push(this.$store.state.end_first_sample[0].sample_id);
                samples.push(first_interval);
            }
            if (this.$store.state.start_second_sample.length > 0 && this.$store.state.end_second_sample.length > 0){
                let second_interval = []
                second_interval.push(this.$store.state.start_second_sample[0].sample_id);
                second_interval.push(this.$store.state.end_second_sample[0].sample_id);
                samples.push(second_interval)
            }
            apiClient.post_create_report(servers, samples, this.job_name, localStorage.getItem('pg_profile_token')).then((response)=>{
                console.log(response.data)
                this.job_id = response.data[0],
                this.snackbar = true;
            })
        },
        get_series(s_f, e_f, s_s, e_s, server){
            let data = []
            let serverName = '';
            try {
                serverName = server[0].serverName
            } catch(err){
                serverName = ''
            }
            if (s_f.length > 0 && e_f.length > 0){
                let entity = {
                    name: 'sample [' + s_f[0].sample_id + ' - ' + e_f[0].sample_id + ']',
                    data: [
                        {
                            x: serverName,
                            y: [
                                new Date(s_f[0].sample_time).getTime(),
                                new Date(e_f[0].sample_time).getTime(),
                            ]
                        }
                    ]
                }
                data.push(entity)
            }
            if (s_s.length > 0 && e_s.length > 0){
                let entity = {
                    name: 'sample [' + s_s[0].sample_id + ' - ' + e_s[0].sample_id + ']',
                    data: [
                        {
                            x: serverName,
                            y: [
                                new Date(s_s[0].sample_time).getTime(),
                                new Date(e_s[0].sample_time).getTime(),
                            ]
                        }
                    ]
                }
                data.push(entity)
            }
            return data
        },
        get_server_info(data){
            try {
               let info = data.serverName + ' ID [' + data.serverId + ']'
               return info
            } catch (err) {
               return ''
            }
        },
        get_first_report_interval(first_sample, second_sample){
            let end_time = ''
            let start_time = ''
            try {
                start_time = first_sample.sample_time

            } catch(err) {
                start_time = ''
            }
            try {
                end_time = second_sample.sample_time
            } catch(err) {
                end_time = ''
            }
            return end_time + ' - ' + start_time
        }
    }
  }
</script>
