<template>
  <v-card-text>
        <v-toolbar
          flat
          color="primary"
          dark
        >
          <v-toolbar-title>Compare queries info</v-toolbar-title>
        </v-toolbar>
        <v-tabs vertical>
          <v-tab>
            First server
          </v-tab>
          <v-tab>
            Second server
          </v-tab>
          <v-tab text-left>
            Queries compare
          </v-tab>

          <v-tab-item>
            <v-card flat>
              <v-card-text>
                <QueriesDataTable v-bind:queries="this.$store.state.queries_first"/>
              </v-card-text>
            </v-card>
          </v-tab-item>
          <v-tab-item>
            <v-card flat>
              <v-card-text>
                <QueriesDataTable v-bind:queries="this.$store.state.queries_seconds"/>
              </v-card-text>
            </v-card>
          </v-tab-item>
          <v-tab-item>
            <v-card flat>
              <v-card-text class="mt-4">
                <v-row>
                  <v-col>
                    <v-card>
                        <v-tabs
                          v-model="tab"
                          background-color="primary"
                          dark
                        >
                          <v-tab>
                            Miss queries
                          </v-tab>
                        </v-tabs>
                        <v-tabs-items v-model="tab">
                          <v-tab-item>
                            <v-card flat>
                              <QueriesMiss v-bind:queries="this.GET_MISS_QUERIES()"/>
                            </v-card>
                          </v-tab-item>
                        </v-tabs-items>
                      </v-card>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-card>
          </v-tab-item>
        </v-tabs>
  </v-card-text>
</template>

<script>
  import {mapGetters} from 'vuex'
  import {mapMutations} from 'vuex'
  import {mapActions} from 'vuex'
  import QueriesDataTable from '@/components/QueriesDataTable.vue'
  import QueriesMiss from '@/components/QueriesMiss.vue'

  export default {
    name: 'QueryCompareComponent',
    props: ['search'],
    components: {QueriesDataTable, QueriesMiss},
    data () {
      return {
        headers: [
          { text: 'Sample id', value: 'sample_id' },
          { text: 'Server id', value: 'server_id' },
          { text: 'Server name', value: 'server_name' },
          { text: 'Server create date', value: 'create_date' },
          { text: 'Sample time', value: 'sample_time' },
          { text: 'select', value: 'select' },
        ],
        compare_data: '',
        sample: '',
        tab: null
      }
    },
    created(){
        this.REFRESH_SAMPLES();
    },

    methods: {
       ...mapGetters(['GET_SAMPLES','GET_QUERIES_MISS', 'GET_MISS_QUERIES']),
       ...mapMutations([
        'SET_START_FIRST_SAMPLE_TO_STATE',
        'SET_END_FIRST_SAMPLE_TO_STATE',
        'SET_START_SECOND_SAMPLE_TO_STATE',
        'SET_END_SECOND_SAMPLE_TO_STATE',
       ]),
       ...mapActions(['REFRESH_SAMPLES']),
    }
  }
</script>