<template>
  <v-card-text>
    <v-col cols="9">
    </v-col>
    <v-col cols="12">
      <v-card>
        <v-card-title>
          Samples
          <v-spacer></v-spacer>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="GET_SAMPLES()"
          :search="search"
          item-key="uuid"
        >
            <template v-slot:item.select = "{ item }">
               <v-checkbox
                v-model="sample"
                v-bind:value="item"
               ></v-checkbox>
            </template>
        </v-data-table>
      </v-card>
    </v-col>
  </v-card-text>
</template>

<script>
  import {mapGetters} from 'vuex'
  import {mapMutations} from 'vuex'
  import {mapActions} from 'vuex'

  export default {
    name: 'SamplesDataTableShort',
    props: ['search','step_num'],
    data () {
      return {
        headers: [
          { text: 'Sample id', value: 'sample_id' },
          { text: 'Server id', value: 'server_id' },
          { text: 'Server name', value: 'server_name' },
          { text: 'Server create date', value: 'create_date' },
          { text: 'Sample time', value: 'sample_time' },
          { text: 'select', value: 'select' },
        ],
        sample: ''
      }
    },
    created(){
        this.REFRESH_SAMPLES()
    },

    methods: {
       ...mapGetters(['GET_SAMPLES','GET_SERVERS']),
       ...mapMutations([
            'SET_FIRST_Q_TO_STATE',
            'SET_SECOND_Q_TO_STATE',
            'SET_SERVER_INFO_STATE'
       ]),
       ...mapActions(['REFRESH_SAMPLES'])
    },
    watch: {
       sample(){
          if (this.step_num==1){
            this.SET_FIRST_Q_TO_STATE(this.sample);
          } else {
            this.SET_SECOND_Q_TO_STATE(this.sample);
          }
       }
    }
  }
</script>