import Vue from 'vue'
import Vuex from 'vuex'
import apiClient from "./ApiClient.js"
import queries from "../data_examples/queries.json"
import queries_second from "../data_examples/queries_second.json"

Vue.use(Vuex);

let store = new Vuex.Store({
    state: {
        email: '',
        home_url: '',
        servers: [],
        select_servers: [],
        samples: [],
        reports: [],
        sSelect: [],
        start_first_sample: [],
        end_first_sample: [],
        start_second_sample: [],
        end_second_sample: [],
        queries_first: queries,
        queries_seconds: queries_second,
        first_queries_compare: {},
        second_queries_compare: {},
        compare_data: {},
        queries_miss: {}
    },
    mutations: {
        SET_QUERIES_MISS:(state, queries_miss)=>{
            state.queries_miss = queries_miss
        },

        SET_COMPARE_QUERY_TO_STATE:(state)=>{
            let miss_queries = []
            let hit_queries = []
            for (let index in state.queries_first.length){
                let query = state.queries_first[index]
                let count = 0;
                for (let s_index in queries_second.length){
                    if (query.queryId==queries_second[s_index].queryId){
                        hit_queries.push(queries)
                        count=count+1;
                    }
                }
                if (count==0){
                    miss_queries.push(query);
                }
            }
            let compare_data = {
                "miss_queries": miss_queries,
                "hit_queries": hit_queries
            }

            state.compare_data=compare_data;
        },
        SET_FIRST_Q_TO_STATE:(state, first_queries_compare)=>{
             state.first_queries_compare = first_queries_compare;
             for (let server in state.servers){
                    let server_entity = state.servers[server]
                    if (server_entity.server_id == first_queries_compare.server_id){
                        state.first_queries_compare['server_info'] = server_entity
                    }
             }
             console.log("mutation first_queries_compare: " + first_queries_compare)
        },
        SET_SECOND_Q_TO_STATE:(state, second_queries_compare)=>{
             state.second_queries_compare = second_queries_compare;
             for (let server in state.servers){
                    let server_entity = state.servers[server]
                    if (server_entity.server_id == second_queries_compare.server_id){
                        state.second_queries_compare['server_info'] = server_entity
                    }
             }
             console.log("mutation second_queries_compare: " + second_queries_compare)
        },
        SET_SELECTS_SERVER_TO_STATE:(state, select_servers)=>{
             state.select_servers = select_servers;
             console.log("mutation select_servers: " + select_servers)
        },
        SET_FIRST_QUERIES_TO_STATE:(state, queries)=>{
             state.queries_first = queries;
             console.log("mutation queries: " + queries)
        },
        SET_SECOND_QUERIES_TO_STATE:(state, queries)=>{
             state.queries_seconds = queries;
             console.log("mutation queries: " + queries)
        },
        SET_SERVERS_TO_STATE:(state, servers)=>{
             state.servers = servers;
             console.log("mutation servers: " + servers)
        },
        SET_REPORTS_TO_STATE:(state, reports)=>{
             state.reports = reports;
             console.log("mutation reports: " + reports)
        },
        SET_SAMPLES_TO_STATE:(state, samples)=>{
             state.samples = samples;
             console.log("mutation samples: " + samples)
        },
        SET_SELECT_SERVER_TO_STATE:(state, select)=>{
             state.sSelect = select;
             console.log("mutation select: " + select)
        },
        SET_START_FIRST_SAMPLE_TO_STATE:(state, start_first_sample)=>{
             state.start_first_sample = start_first_sample;
             console.log("mutation start_first_sample: " + start_first_sample)
        },
        SET_END_FIRST_SAMPLE_TO_STATE:(state, end_first_sample)=>{
             state.end_first_sample = end_first_sample;
             console.log("mutation end_first_sample: " + end_first_sample)
        },
        SET_START_SECOND_SAMPLE_TO_STATE:(state, start_second_sample)=>{
             state.start_second_sample = start_second_sample;
             console.log("mutation start_second_sample: " + start_second_sample)
        },
        SET_END_SECOND_SAMPLE_TO_STATE:(state, end_second_sample)=>{
             state.end_second_sample = end_second_sample;
             console.log("mutation end_second_sample: " + end_second_sample)
        }
    },
    actions: {
        REFRESH_SERVERS({commit}){
            apiClient.get_servers().then((response)=>{
                console.log(response.data)
                commit('SET_SERVERS_TO_STATE', response.data);
                let select_server = [];
                for (let server in response.data){
                    console.log('SERVER SELECT: ' + response.data[server])
                    select_server.push(response.data[server].serverName);
                }
                commit('SET_SELECTS_SERVER_TO_STATE', select_server);
            });
        },
        REFRESH_SAMPLES({commit}){
            apiClient.get_samples().then((response)=>{
                console.log(response.data)
                commit('SET_SAMPLES_TO_STATE', response.data);
            });
        },
        REFRESH_JOBS({commit}){
            apiClient.get_reports().then((response)=>{
                console.log(response.data.content)
                commit('SET_REPORTS_TO_STATE', response.data.content);
            });
        },
        GET_QUERIES_FOR_FIRST({commit}){
            apiClient.get_queries_by_sample(
                this.state.first_queries_compare.sample_id,
                this.state.first_queries_compare.server_id
            ).then((response)=>{
                console.log(response.data)
                commit('SET_FIRST_QUERIES_TO_STATE', response.data);
            });
        },
        GET_QUERIES_FOR_SECOND({commit}){
            apiClient.get_queries_by_sample(
                this.state.second_queries_compare.sample_id,
                this.state.second_queries_compare.server_id
            ).then((response)=>{
                console.log(response.data)
                commit('SET_SECOND_QUERIES_TO_STATE', response.data);
            });
        },
        GET_MISS_QUERIES({commit}){
            apiClient.get_queries_compare(
                this.state.first_queries_compare.server_id,
                this.state.first_queries_compare.sample_id,
                this.state.second_queries_compare.server_id,
                this.state.second_queries_compare.sample_id
            ).then((response)=>{
                console.log(response.data)
                commit('SET_QUERIES_MISS', response.data.except);
            });
        }
    },
    getters: {
        GET_MISS_QUERIES(state){
            return state.queries_miss
        },
        GET_FIRST_Q(state){
            return state.first_queries_compare
        },
        GET_SECOND_Q(state){
            return state.second_queries_compare
        },
        GET_SELECT_SERVERS(state){
            return state.select_servers;
        },
        GET_FIRST_QUERIES(state){
            return state.queries_first;
        },
        GET_SECONDS_QUERIES(state){
            return state.queries_seconds;
        },
        GET_SERVERS(state){
            return state.servers;
        },
        GET_SAMPLES(state){
            return state.samples;
        },
        GET_SELECT_SERVER(state){
            return state.sSelect;
        },
        GET_REPORTS(state){
            return state.reports;
        },
        GET_START_FIRST_SAMPLE(state){
            return state.start_first_sample;
        },
        GET_END_FIRST_SAMPLE(state){
            return state.end_first_sample;
        },
        GET_START_SECOND_SAMPLE(state){
            return state.start_second_sample;
        },
        GET_END_SECOND_SAMPLE(state){
            return state.end_second_sample;
        }
    }
});

export default store;