<template>
    <v-app primary>
      <v-app-bar
        app
        color="light-blue darken-4"
        dark
      >
        <v-app-bar-nav-icon @click="drawer=true"></v-app-bar-nav-icon>
        <v-avatar :tile="true" size="36">
          <v-img
            src="@/assets/pg_logo.png" alt="logo"
          ></v-img>
        </v-avatar>
        <v-spacer>
        </v-spacer>
        <LogoutDialog/>
      </v-app-bar>
      <v-navigation-drawer
        absolute
        temporary
        v-model="drawer"
      >
        <NavigationDrawer/>
      </v-navigation-drawer>
      <v-main>
        <v-breadcrumbs :items="items" large></v-breadcrumbs>
        <v-container fluid>
          <v-row>
            <v-col cols="6">
              <v-card outlined>
              </v-card>
            </v-col>
            <v-col cols="6">
              <v-card outlined>
              </v-card>
            </v-col>
            <v-col cols="12">
              <JobsContentView/>
            </v-col>
          </v-row>
        </v-container>
      </v-main>
    </v-app>
</template>

<script>
  import NavigationDrawer from '@/components/NavigationDrawer.vue'
  import LogoutDialog from '@/components/dialogs/LogoutDialog.vue'
  import JobsContentView from '@/components/JobsContentView.vue'

  export default {
    name: 'DashboardPage',
    components: {
        NavigationDrawer,
        LogoutDialog,
        JobsContentView
    },
    created(){
       if (!localStorage.getItem('pg_profile_token')){
          this.$router.push({ path: '/login' })
       }
    },
    data: () => ({
      drawer: false,
      items: [
        {
          text: 'Reports',
          disabled: true,
          href: '/',
        }
      ],
    }),
  }
</script>
