<template>
    <v-app primary>
      <v-app-bar
        app
        color="light-blue darken-4"
        dark
      >
        <v-app-bar-nav-icon @click="drawer=true"></v-app-bar-nav-icon>
        <v-avatar :tile="true" size="36">
          <v-img
            src="@/assets/pg_logo.png" alt="logo"
          ></v-img>
        </v-avatar>
        <v-spacer>
        </v-spacer>
        <LogoutDialog/>
      </v-app-bar>
      <v-navigation-drawer
        absolute
        temporary
        v-model="drawer"
      >
        <NavigationDrawer/>
      </v-navigation-drawer>
      <v-main>
        <v-breadcrumbs :items="items" large></v-breadcrumbs>

        <v-container fluid>
          <v-row>
            <v-col md="12">
                <v-card outlined>
                   <v-card-title>
                    Create new instance
                   </v-card-title>
                   <v-card-text>
                    <div class="text--primary">
                      <v-row>
                        <v-col md=3>
                            <v-text-field
                                  label="Host"
                                  hide-details="auto"
                                  v-model="payload.host"
                            ></v-text-field>
                            <v-text-field
                                  label="Port"
                                  v-model="payload.port"
                                  hide-details="auto"
                            ></v-text-field>
                            <v-text-field
                                  label="Database"
                                  v-model="payload.database"
                                  hide-details="auto"
                            ></v-text-field>
                            <v-text-field
                                  label="User"
                                  v-model="payload.user"
                                  hide-details="auto"
                            ></v-text-field>
                            <v-text-field
                                  label="Password"
                                  v-model="payload.password"
                                  hide-details="auto"
                            ></v-text-field>
                        </v-col>
                        <v-col md=3>
                            <v-text-field
                                  label="Name"
                                  v-model="payload.name"
                                  hide-details="auto"
                            ></v-text-field>
                            <v-textarea
                               class="mt-4"
                               outlined
                               row-height="30"
                               rows="6"
                               v-model="payload.description"
                               label="Server description"
                            ></v-textarea>
                        </v-col>

                      </v-row>
                    </div>
                   </v-card-text>
                   <v-card-actions>
                     <v-btn
                       color="primary"
                       outlined
                       small
                       @click="create_server()"
                     >
                       Create instance
                     </v-btn>
                   </v-card-actions>
                </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-main>
    </v-app>
</template>

<script>
  import NavigationDrawer from '@/components/NavigationDrawer.vue'
  import LogoutDialog from '@/components/dialogs/LogoutDialog.vue'
  import apiClient from "../../plugins/ApiClient.js"

  export default {
    name: 'CreateNewInstance',
    components: {
        NavigationDrawer,
        LogoutDialog,
    },
    data: () => ({
      payload : {
         'host': '',
         'port': '',
         'database': '',
         'user': '',
         'password': '',
         'name': '',
         'description': ''
      },
      drawer: false,
      group: null,
      items: [
         {
            text: 'Home',
            disabled: false,
            href: 'breadcrumbs_dashboard',
         },
         {
            text: 'add',
            disable: false,
            href: 'breadcrumbs_dashboard'
         }
      ],
    }),
    methods: {
        create_server(){
            apiClient.create_server(this.payload)
        }
    },
    created(){
        if (!localStorage.getItem('pg_profile_token')){
            this.$router.push({ path: '/login' })
        }
    },
  }
</script>
