<template>
    <v-container class="fill-height" fluid>
        <v-layout flex align-center justify-center>
         <v-flex xs4>
           <v-container style="position: relative;top: 13%;" class="text-xs-center">
             <v-card>
               <v-card-title primary-title>
                <v-row>
                   <v-col>
                    <v-avatar :tile="true" size="62">
                        <v-img
                           src="@/assets/pg_logo.png" alt="logo"
                        ></v-img>
                    </v-avatar>
                   </v-col>
                   <v-col>
                    PG_PROFILE [zubkov-andrei]
                   </v-col>
                </v-row>
               </v-card-title>
               <v-card-text class="font-weight-medium mt-12 text-center text-subtitle-1">
               <v-form class="ma-3">
                   <v-text-field prepend-icon="" name="email" label="email" v-model="username"></v-text-field>
                   <v-text-field prepend-icon="" name="Password" label="Password" type="password" v-model="password"></v-text-field>
                   <v-card-actions>
                     <v-btn color="light-blue darken-4" large block dark @click="post_auth()">Login</v-btn>
                   </v-card-actions>
               </v-form>
               </v-card-text>
             </v-card>
           </v-container>
         </v-flex>
       </v-layout>
   </v-container>
</template>

<script>
import apiClient from "../../plugins/ApiClient.js"

export default {
  name: "App",
  data() {
    return {
      username: '',
      password: '',
      email: ''
    };
  },
  methods: {
    post_auth(){
        console.log('auth try')
        apiClient.auth(this.username, this.password).then((response)=>{
            let token = response.data.token
            this.$store.state.email = response.data.email
            localStorage.setItem('pg_profile_email', response.data.email);
            console.log(token)
            localStorage.setItem('pg_profile_token', token);
            this.$router.push({ path: '/' })
        })
    }
  },
  computed: {
    toggleMessage : function() {
       return this.isRegister ? this.stateObj.register.message : this.stateObj.login.message
    }
  },
  created(){

    if (localStorage.getItem('pg_profile_token')){
        this.$router.push({ path: '/' })
    }
  }
};
</script>
