<template>
    <v-card-text>
      <v-row>
        <v-col
          class="d-flex"
          cols="12"
          sm="12"
        >
         <v-row>
          <v-col cols="12">
              <v-select
                 :items="this.GET_SELECT_SERVERS()"
                 label="server"
                 v-model="select"
                 outlined
              >
              </v-select>
          </v-col>
          <v-col cols="12">
            <SamplesDataTableShort v-bind:search="select" v-bind:step_num="step_num"/>
          </v-col>
         </v-row>
        </v-col>
      </v-row>
    </v-card-text>
</template>

<script>
  import {mapGetters} from 'vuex'
  import {mapMutations} from 'vuex'
  import {mapActions} from 'vuex'
  import SamplesDataTableShort from '@/components/SamplesDataTableShort.vue'

  export default {
    name: 'SelectServerComponent',
    components: {
        SamplesDataTableShort
    },
    props: ['step_num'],
    data () {
      return {
        server: [],
        select: 'local',
        server_desc: ''
      }
    },
    methods: {
        ...mapGetters(['GET_SERVERS', 'GET_SELECT_SERVERS', 'GET_FIRST_Q', 'GET_SECOND_Q']),
        ...mapMutations(['SET_SELECT_SERVER_TO_STATE','SET_FIRST_Q_TO_STATE','SET_SECOND_Q_TO_STATE', 'SET_SERVER_INFO_STATE']),
        ...mapActions(['REFRESH_SERVERS']),

        server_data(){
            let sample = {};
            if (this.step_num==1){
                sample = this.GET_FIRST_Q();
                this.SET_SERVER_INFO_STATE(sample.server_id)
            } else {
                sample = this.GET_SECOND_Q();
                this.SET_SERVER_INFO_STATE(sample.server_id)
            }
        }
    },

    created(){
        this.REFRESH_SERVERS();
        this.preset();
    }
  }
</script>