import Vue from 'vue'
import VueRouter from 'vue-router'
import LoginPage from '../components/pages/LoginPage.vue'
import DashboardPage from '../components/pages/HomeDashboardPage.vue'
import ServerDetails from '../components/pages/ServerDetails.vue'
import CreateNewInstance from '../components/pages/CreateNewInstancePage.vue'
import ReportsPage from '../components/pages/ReportsPage.vue'
import QueryAnalyse from '../components/pages/QueryAnalysePage.vue'

const routes = [
  {
    path: '/',
    component: DashboardPage,
    name: 'Dashboard'
  },
  {
    path: '/reports',
    component: ReportsPage,
    name: 'ReportsPage'
  },
  {
    path: '/login',
    component: LoginPage,
    name: 'Login'
  },
  {
    path: '/server/:id',
    component: ServerDetails,
    name: 'ServerDetails'
  },
  {
    path: '/server/add',
    component: CreateNewInstance,
    name: 'CreateNewInstance'
  },
  {
    path: '/queries',
    component: QueryAnalyse,
    name: 'QueryAnalyse'
  }
]

Vue.use(VueRouter)
export default new VueRouter({
  routes
})