<template>
  <v-row>
    <v-col cols="12">
        <v-card-title>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
          <v-data-table
            :headers="headers"
            :items="queries"
            :expanded.sync="expanded"
            item-key="queryidMd5"
            show-expand
            :search="search"
            class="elevation-1"
          >
            <template v-slot:expanded-item="{ headers, item }">
              <td :colspan="headers.length">
                <v-card outlined class="mt-3 mb-3 ml-2 mr-2">
                  <v-card-text>
                     <v-textarea
                          label="QUERY"
                          readonly
                          background-color="grey lighten-2"
                          color="cyan"
                          :value="item.query"
                     ></v-textarea>
                     <v-row>
                        <v-col cols="2">
                         <p><b>shared_blks_hit: </b> {{item.shared_blks_hit}}</p>
                         <p><b>shared_blks_read: </b> {{item.shared_blks_read}}</p>
                         <p><b>shared_blks_dirtied: </b> {{item.shared_blks_dirtied}}</p>
                         <p><b>shared_blks_written: </b> {{item.shared_blks_written}}</p>

                         <p><b>local_blks_hit: </b> {{item.local_blks_hit}}</p>
                         <p><b>local_blks_read: </b> {{item.local_blks_read}}</p>
                         <p><b>local_blks_dirtied: </b> {{item.local_blks_dirtied}}</p>
                         <p><b>local_blks_written: </b> {{item.local_blks_written}}</p>
                        </v-col>
                        <v-col cols="2">
                         <p><b>temp_blks_dirtied: </b> {{item.temp_blks_dirtied}}</p>
                         <p><b>temp_blks_written: </b> {{item.temp_blks_written}}</p>
                         <p><b>blk_read_time: </b> {{item.blk_read_time}}</p>
                         <p><b>blk_write_time: </b> {{item.blk_write_time}}</p>
                         <p><b>wal_records: </b> {{item.wal_records}}</p>
                         <p><b>wal_fpi: </b> {{item.wal_fpi}}</p>
                         <p><b>wal_bytes: </b> {{item.wal_bytes}}</p>
                         <p><b>rows: </b> {{item.rows}}</p>
                         <p><b>stddev_exec_time: </b> {{item.stddev_exec_time}} </p>
                        </v-col>
                        <v-col>
                          <v-row>
                            <v-col>
                              <apexchart type="pie" width="380" :options="chartOptions" :series="series_hit_read(item)"></apexchart>
                            </v-col>
                          </v-row>
                        </v-col>
                     </v-row>
                  </v-card-text>
                </v-card>
              </td>
            </template>
          </v-data-table>
    </v-col>
  </v-row>
</template>

<script>
  import {mapGetters} from 'vuex'
  import {mapMutations} from 'vuex'
  import {mapActions} from 'vuex'

  export default {
    name: 'QueriesMissTable',
    props: ['queries'],
    data () {
      return {
        expanded: false,
        search: '',
        headers: [
          { text: 'serverId', value: 'serverid'},
          { text: 'sampleId', value: 'sampleid'},
          { text: 'queryidMd5', value: 'queryidMd5'},
          { text: 'queryId', value: 'queryId' },
          { text: 'userId', value: 'userId' },
          { text: 'datId', value: 'datId' },
          { text: 'calls', value: 'calls' },
          { text: 'total_exec_time', value: 'total_exec_time' },
          { text: 'min_exec_time', value: 'min_exec_time'},
          { text: 'max_exec_time', value: 'max_exec_time'},
          { text: 'mean_exec_time', value: 'mean_exec_time'}
        ],
        chartOptions: {
            chart: {
               width: 250,
               type: 'pie',
            },
            labels: ['shared_blks_hit', 'shared_blks_read'],
            responsive: [
                {
                    breakpoint: 480,
                    options: {
                        chart: {
                            width: 200
                        },
                        legend: {
                          position: 'bottom'
                        }
                    }
                }
            ]
        },
        series_exec_time: [15, 85],
        chartOptionsExecTime: {
            chart: {
               width: 250,
               type: 'pie',
            },
            labels: ['mean_exec_time', 'total_exec_time'],
            responsive: [
                {
                    breakpoint: 480,
                    options: {
                        chart: {
                            width: 200
                        },
                        legend: {
                          position: 'bottom'
                        }
                    }
                }
            ]
        },
        series_exec_count: [15, 85],
        chartOptionsExecTimeCount: {
            chart: {
               width: 250,
               type: 'pie',
            },
            labels: ['calls', 'total_query_calls'],
            responsive: [
                {
                    breakpoint: 480,
                    options: {
                        chart: {
                            width: 200
                        },
                        legend: {
                          position: 'bottom'
                        }
                    }
                }
            ]
        }
      }
    },
    created(){
        this.REFRESH_SAMPLES()
    },

    methods: {
       ...mapGetters(['GET_SAMPLES']),
       ...mapMutations([
        'SET_START_FIRST_SAMPLE_TO_STATE',
        'SET_END_FIRST_SAMPLE_TO_STATE',
        'SET_START_SECOND_SAMPLE_TO_STATE',
        'SET_END_SECOND_SAMPLE_TO_STATE'
       ]),
       ...mapActions(['REFRESH_SAMPLES']),
       series_hit_read(item){
           let series = []
           series.push(item.shared_blks_hit)
           series.push(item.shared_blks_read)
           return series;
       }
    },
    watch: {

    },
  }
</script>