import axios from 'axios'

var apiClient = {
  server_endpoint: '',
  api: {
     post_auth: 'api/v1/uiadmin/auth',
     get_servers: 'api/v1/uiadmin/servers',
     post_new_server: 'api/v1/uiadmin/server',
     get_samples: 'api/v1/uiadmin/samples',
     get_reports: 'api/v1/uiadmin/reports',
     get_queries: 'api/v1/uiadmin/queries',
     get_queries_compare: 'api/v1/uiadmin/queries/compare'
  },

  ///api/v1/uiadmin/queries/compare?firstServerId=1&firstSampleId=20&secondServerId=2&secondSampleId=10
  get_queries_compare(firstServerId, firstSampleId, secondServerId, secondSampleId){
    var config = {
        headers: {
            'content-type': 'application/json',
            'token': localStorage.getItem('pg_profile_token')
        },
        method: 'GET'
    };
    return axios(this.server_endpoint + this.api.get_queries_compare + '?firstServerId=' + firstServerId + '&firstSampleId=' + firstSampleId + '&secondServerId=' + secondServerId + '&secondSampleId=' + secondSampleId, config);
  },

  get_queries_by_sample(sampleId, serverId){
    var config = {
        headers: {
            'content-type': 'application/json',
            'token': localStorage.getItem('pg_profile_token')
        },
        method: 'GET'
    };
    return axios(this.server_endpoint + this.api.get_queries + '?serverId=' + serverId + '&sampleId=' + sampleId, config);
  },
  auth(email, pass) {
    console.log(this.type);
    let data = {
       email: email,
       password: pass
    }
    var config = {
        headers: {'content-type': 'application/json'},
        method: 'POST'
    };
    return axios.post(this.server_endpoint + this.api.post_auth, data, config)
  },
  post_create_sample(data, token){
    let payload = {
       "servers": data
    }
    var config = {
      headers: {
        'content-type': 'application/json',
        'token': token
      },
      method: 'POST'
    };
    return axios.post(this.server_endpoint + this.api.get_samples, payload, config)
  },
  post_create_report(server, samples, name, token){
    let payload = {
        "name": name,
        "samples": {
            "samples": samples
        },
        "servers": server
    }
    var config = {
      headers: {
        'content-type': 'application/json',
        'token': token
      },
      method: 'POST'
    };
    return axios.post(this.server_endpoint + this.api.get_reports, payload, config)
  },
  get_servers(){
    var config = {
        headers: {
            'content-type': 'application/json',
            'token': localStorage.getItem('pg_profile_token')
        },
        method: 'GET'
    };
    return axios(this.server_endpoint + this.api.get_servers, config);
  },
  get_samples(){
    var config = {
        headers: {
            'content-type': 'application/json',
            'token': localStorage.getItem('pg_profile_token')
        },
        method: 'GET'
    };
    return axios(this.server_endpoint + this.api.get_samples, config);
  },
  get_reports(){
    var config = {
        headers: {
            'content-type': 'application/json',
            'token': localStorage.getItem('pg_profile_token')
        },
        method: 'GET'
    };
    return axios(this.server_endpoint + this.api.get_reports, config);
  },
  create_server(payload){
    let config = {
          headers: {
            'content-type': 'application/json',
            'token': localStorage.getItem('pg_profile_token')
          },
          method: 'POST'
    };
    console.log(config)
    console.log(payload)
    return axios.post(this.server_endpoint + this.api.post_new_server, payload, config)
  },
  get_server_details(id){
    var config = {
        headers: {
            'content-type': 'application/json',
            'token': localStorage.getItem('pg_profile_token')
        },
        method: 'GET'
    };
    return axios(this.server_endpoint + this.api.get_servers + '/' + id, config);
  },
};

export default apiClient