<template>
  <v-row>
    <v-col cols="12">
      <v-card>
        <v-card-title>
          Servers
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="GET_SERVERS()"
          :search="search"
          item-key="serverId"
          v-model="selected"
          :single-select="singleSelect"
          show-select
        >
            <template v-slot:item.Actions="{item}">
              <v-tooltip left>
                 <template v-slot:activator="{ on, attrs }">
                   <v-btn
                      color="primary"
                      v-bind="attrs"
                      v-on="on"
                      outlined
                      small
                      :to="{ name: 'ServerDetails', params: {id: item.serverId}}"
                   >
                      Details
                   </v-btn>
                 </template>
                 <span>{{item.serverDescription}}</span>
              </v-tooltip>
            </template>
        </v-data-table>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
  import {mapGetters} from 'vuex'
  import {mapMutations} from 'vuex'
  import {mapActions} from 'vuex'

  export default {
    name: 'ProjectsDataTable',
    components: {
    },
    data () {
      return {
        singleSelect: true,
        selected: [],
        search: '',
        headers: [
          { text: 'Server Id', value: 'serverId' },
          { text: 'Server name', value: 'serverName' },
          { text: 'Create date', value: 'serverCreated' },
          { text: 'Max sample age', value: 'maxSampleAge' },
          { text: 'Last sample id', value: 'lastSampleId' },
          { text: 'Size smp' , value: 'sizeSmpInterval'},
          { text: 'Size smp wnd' , value: 'sizeSmpWndDur'},
          { text: 'Enabled', value: 'enabled'},
          { text: 'Actions', value: 'Actions'}
        ],

      }
    },
    methods: {
        server_url_builder(id){
            return '/server/' + id
        },
        ...mapGetters(['GET_SERVERS']),
        ...mapMutations(['SET_SELECT_SERVER_TO_STATE']),
        ...mapActions(['REFRESH_SERVERS'])
    },
    created(){
        this.REFRESH_SERVERS();
    },
    watch: {
       selected(){
          this.SET_SELECT_SERVER_TO_STATE(this.selected)
       }
    }
  }
</script>