<template>
    <v-app primary>
      <v-app-bar
        app
        color="light-blue darken-4"
        dark
      >
        <v-app-bar-nav-icon @click="drawer=true"></v-app-bar-nav-icon>
        <v-avatar :tile="true" size="36">
          <v-img
            src="@/assets/pg_logo.png" alt="logo"
          ></v-img>
        </v-avatar>
        <v-spacer>
        </v-spacer>
        <CreateEntityDialog/>
        <LogoutDialog/>
      </v-app-bar>
      <v-navigation-drawer
        absolute
        temporary
        v-model="drawer"
      >
        <NavigationDrawer/>
      </v-navigation-drawer>
      <v-main>
        <v-breadcrumbs :items="items" large></v-breadcrumbs>

        <v-container fluid>
          <v-row>
            <v-col md="12">
                <v-card outlined>
                   <v-card-title>
                    {{server.serverName}}
                   </v-card-title>
                   <v-card-text>
                    <div class="text--primary">
                        {{server.serverDescription}}
                    </div>
                   </v-card-text>
                   <v-card-actions>
                   </v-card-actions>
                </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-main>
    </v-app>
</template>

<script>
  import NavigationDrawer from '@/components/NavigationDrawer.vue'
  import CreateEntityDialog from '@/components/dialogs/CreateEntityDialog.vue'
  import LogoutDialog from '@/components/dialogs/LogoutDialog.vue'
  import apiClient from "../../plugins/ApiClient.js"

  export default {
    name: 'ServerDetails',
    components: {
        NavigationDrawer,
        CreateEntityDialog,
        LogoutDialog,
    },
    data: () => ({
      drawer: false,
      group: null,
      items: [
         {
            text: 'Home',
            disabled: false,
            href: 'breadcrumbs_dashboard',
         },
         {
            text: '1',
            disable: false,
            href: 'breadcrumbs_dashboard'
         }
      ],
      server: {
        id: '12',
        server_name: 'PostgreSQL SE [SHRS]',
        ip: '192.168.5.11',
        dns: 'tvldi-216.delta.sbrf.ru',
        create: '2022-02-04 21:19:39.893 +0300'
      }
    }),
    methods: {

    },
    created(){
       this.items[1].text = this.$route.params.id;
       if (!localStorage.getItem('pg_profile_token')){
          this.$router.push({ path: '/login' })
       }
       apiClient.get_server_details(this.$route.params.id).then((response)=>{
           console.log(response.data)
           this.server = response.data;
       })
    }
  }
</script>
